<template>
  <div id="app">
    <!--start rock , paper , scissors-->
    <article>
      <section class="central-project">
        <div class="container mx-auto p-3">
          <div class="bg-gray-600 w-full min-h-screen flex flex-col items-center justify-center p-3">
            <h1 class="text-center text-white text-2xl font-semibold uppercase top-0 m-3">rock , paper , scissors</h1>
            <div class="flex flex-row items-center justify-between" v-if="choice===''">
              <div class="w-4/12 h-56">
                <button @click="play('rock')"
                        class="bg-white text-center items-center rounded-full px-3 py-3 hover:text-slate-400 hover:bg-pink-400 transition-all duration-300">
                  <img :src="require('../assets/images/rock-4.png')" alt="rock"
                       class="w-full h-56 overflow-hidden rounded-full">
                </button>
              </div>
              <div class="w-4/12 h-56">
                <button @click="play('paper')"
                        class="bg-white text-center items-center rounded-full px-3 py-3 hover:text-slate-400 hover:bg-violet-400 transition-all duration-300">
                  <img :src="require('../assets/images/paper-3.png')" alt="paper"
                       class="w-full h-56 overflow-hidden rounded-full">
                </button>
              </div>
              <div class="w-4/12 h-56">
                <button @click="play('scissors')"
                        class="bg-white text-center items-center rounded-full px-3 py-3 hover:text-slate-400 hover:bg-green-400 transition-all duration-300">
                  <img :src="require('../assets/images/scissors.png')" alt="scissors"
                       class="w-full h-56 overflow-hidden rounded-full">
                </button>
              </div>
            </div>
            <div v-else class="flex flex-col items-center justify-center text-center mx-auto">
              <div class="text-2xl mb-3">
                you picked is <span class="text-slate-400 font-light">{{ choice }}</span>
              </div>
              <div class="text-2xl mb-3">
                the computed picked is <span class="text-slate-400 font-light">{{ computerChoice }}</span>
              </div>
              <div class="text-3xl mb-3">
                <p class="text-center text-slate-300 font-light">{{ verdict }}</p>
              </div>
              <button @click="resetPlay" type="button" class="bg-pink-500 px-3 py-3 justify-center rounded-md text-center items-center text-white hover:text-slate-800">
                reset
              </button>
              <div class="mt-3 text-2xl mb-3 mx-2">
                <b class="text-green-500">{{ wins }}</b> : <b class="text-red-500">{{ losers }}</b> : <b class="text-orange-500">{{ draws }}</b>
                <ul class="flex flex-row mx-2 items-center">
                  <li v-for="items in list" :key="items" class="m-1">{{items}}</li>
                </ul>
              </div>
              <div class="text-md text-center text-slate-400">
                win rate : {{ Math.round(winPercentage) }}%
              </div>
            </div>
          </div>
        </div>
      </section>
    </article>
  </div>
</template>

<script>
import {ref, reactive, computed, onMounted} from 'vue'

export default {
  name: "BasePromise",
  setup() {
    const wins = ref(0);
    const draws = ref(0);
    const losers = ref(0);
    const choice = ref('');
    const computerChoice = ref('');
    const verdict = ref('')
    const list=reactive(['win :','loss :','draws'])
    const outcomes = reactive({
      rock: {
        rock: 'draw',
        paper: 'loss',
        scissors: 'win'
      },
      paper: {
        paper: 'draw',
        rock: 'win',
        scissors: 'loss'
      },
      scissors: {
        scissors: 'draw',
        rock: 'loss',
        paper: 'win'
      },
    }
)
const winPercentage = computed(() => {
  const sum = wins.value + losers.value + draws.value;
  return sum ? (wins.value / sum) * 100 : 0;
})
const play = ((counter) => {
  choice.value = counter;
  const choices = ['paper', 'rock', 'scissors'];
  const random = Math.floor(Math.random() * choices.length);
  computerChoice.value = choices[random]

  const outcome = outcomes[counter][computerChoice.value];//rock,paper,scissors
  if (outcome === 'win') {
    wins.value++;
    verdict.value = 'you are wins !'
  } else if (outcome === 'loss') {
    losers.value++;
    verdict.value = 'you are loss !'
  } else {
    draws.value++;
    verdict.value = 'you are draws !'
  }
  saveGames()
})
const saveGames = (() => {
  localStorage.setItem('wins', wins.value)
  localStorage.setItem('losers', losers.value)
  localStorage.setItem('draws', draws.value);
})
const loadGames = (() => {
  wins.value = localStorage.getItem('wins') || 0
  draws.value = localStorage.getItem('draws') || 0
  losers.value = localStorage.getItem('losers') || 0
})
const resetPlay = (() => {
  choice.value = ''
  computerChoice.value = ''
  verdict.value = ''
})
const result = onMounted(() => {
  loadGames()

  window.addEventListener('keydown', function (event) {
    if (event.code==='Enter') {
      resetPlay()
    }
  })
})
return {
  wins,
  draws,
  list,
  losers,
  resetPlay,
  choice,
  computerChoice,
  verdict,
  outcomes,
  play,
  result,
  winPercentage,

}
}
}
</script>

